.container-slider{
    max-width: 900px;
    height: 550px;
    margin: 100px auto 0;
    position: relative;
    overflow: hidden;
    box-shadow: 0 10px 20px rgba(0,0,0,0.2);
    border-radius: 50px;
    border: 2px solid blueviolet;
}



.slide{
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    transition: opacity ease-in-out 0.4s;
}

.slide img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.active-anim{
    opacity: 1;
}

.btn-slide{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: #f1f1f1;
    border: 1px solid rgba(34,34,34,0.287);
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.btn-slide img{
    width: 25px;
    height: 25px;
    pointer-events: none;
}
.prev{
    top:50%;
    left: 20px;
    transform: translateY(-60%);
}

.next{
    top:50%;
    right: 20px;
    transform: translateY(-60%);
}

@media screen and (max-width: 800px) {
    .container-slider{
        margin: 100px 10px 0;
    }
    .btn-slide{
        width: 35px;
        height: 35px;
    }
}